import 'moment/locale/zh-cn'
moment.locale('zh-cn');
export default {
	components: {
		reserveDate,
		createComment,
		lzNuminput,
		added
	},
	name: 'popup-layer',
	model: {
		prop: "showPop",
		event: "change",
		btns: "btn",
	},
	props: {
		infoCode: {
			type: String
		},
		cancelConditionArray: {
			type: Array
		},
		showPop: {
			type: Boolean,
			default: false,
		},
		ticketifo: {
			type: Array,
			default () {
				return []
			}
		},
		date: {
			type: Array,
			default () {
				return []
			}
		},
		morendate: {
			type: String
		},
		direction: {
			type: String,
			default: 'bottom', // 方向  top，bottom，left，right 
		},
		autoClose: {
			type: Boolean,
			default: true,
